    display: inline-block;
    padding: 0em 0.10em;
    font-size: 1em;
  }
  
  ul.contact li span
  {
    display: none;
    margin: 0;
    padding: 0;
  }
  
  ul.contact li a
  {
    color: #FFF;
  }
  
  ul.contact li a:before
  {
    display: inline-block;
    background: #A00044;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: rgba(255,255,255,.5);
  }
  </style>



