import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import adduser from '@/components/admin/crear_user'
import profe from '@/components/prof/holaprof'
import est from '@/components/est/holaest'
import admin from '@/components/admin/holaadmin'
import login from '@/components/login'
import luis from '@/components/luis'
import user from '@/components/admin/usuarios'
import editusu from '@/components/admin/editusu'
import horadd from '@/components/admin/nuevohorario'
import horario from '@/components/admin/horarios'
import mivi from '@/components/mivi'
import quienes from '@/components/quienes'
import historia from '@/components/historia'
import vistausua from '@/components/admin/vistausu'
import materias from '@/components/admin/materias'
import editmat from '@/components/admin/editmateria'
import crearmateria from '@/components/admin/crearmateria'

Vue.use(Router)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/login',
    component: login,
    name: 'login'
  },
  {
    path: '/mivi',
    component: mivi,
    name: 'mivi'
  },
  {
    path: '/quienes',
    component: quienes,
    name: 'quienes'
  },
  {
    path: '/historia',
    component: historia,
    name: 'historia'
  },
  {
    path: '/luis',
    component: luis,
    name: 'luis'
  },
  {
    path: '/admin',
    component: admin,
    name: 'admin',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/editmateria',
    component: editmat,
    name: 'editmateria',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/crearmateria',
    component: crearmateria,
    name: 'crearmateria',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/materias',
    component: materias,
    name: 'materias',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/vistausera',
    component: vistausua,
    name: 'vistausera',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/profesor',
    component: profe,
    name: 'prof',
    meta: {requiresAuth: true, residentAuth: true, adminAuth: false, residentAuth1: false}
  },
  {
    path: '/estudiante',
    component: est,
    name: 'est',
    meta: {requiresAuth: true, adminAuth: false, residentAuth: false, residentAuth1: true}
  },
  {
    path: '/user/add',
    component: adduser,
    name: 'adduser',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/user',
    component: user,
    name: 'user',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/editusu',
    component: editusu,
    name: 'editusu',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/hor/add',
    component: horadd,
    name: 'horadd',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/horario',
    component: horario,
    name: 'horario',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  }
]

const router = new Router({routes, mode: 'history'})

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    const authUser = JSON.parse(localStorage.getItem('jwtToken'))
    if (!authUser || !authUser.token) {
      console.log('paso un error de luis')
      next({name: 'Login'})
    } else if (to.meta.adminAuth) {
      const authUser = JSON.parse(localStorage.getItem('jwtToken'))
      if (authUser.data.tipo === 1) {
        console.log('es de tipo luis')
        next()
      } else {
        if (authUser.data.tipo === 2) {
          next({name: 'prof'})
        }
        if (authUser.data.tipo === 3) {
          next({name: 'est'})
        }
      }
    } else if (to.meta.residentAuth) {
      const authUser = JSON.parse(localStorage.getItem('jwtToken'))
      if (authUser.data.tipo === 2) {
        next()
      } else {
        if (authUser.data.tipo === 1) {
          next({name: 'admin'})
        }
        if (authUser.data.tipo === 3) {
          next({name: 'est'})
        }
      }
    } else if (to.meta.residentAuth1) {
      const authUser = JSON.parse(localStorage.getItem('jwtToken'))
      if (authUser.data.tipo === 3) {
        next()
      } else {
        if (authUser.data.tipo === 2) {
          next({name: 'prof'})
        }
        if (authUser.data.tipo === 1) {
          next({name: 'admin'})
        }
      }
    }
  } else {
    next()
  }
})

export default router



