<script>

import PostsService from '@/services/PostsService'
export default {
  name: 'person',
  data () {
    return {
      datos: [],
      datos1: [],
      datos2: [],
      datos3: [],
      nivel: '',
      nombre1: JSON.parse(localStorage.getItem('jwtToken')).data.user
    }
  },
  mounted () {
    this.getdatos()
  },
  methods: {
    async getdatos () {
      const response = await PostsService.mostrarotros()
      const response1 = await PostsService.mostrarhoractual(response.data.niv[0].id)
      this.datos2 = response1.data.hor
      console.log(this.datos2)
      this.datos3 = response.data.niv
      console.log(this.datos3)
    },
    async moshor ({type, target}) {
      this.datos2 = []
      const response = await PostsService.mostrarhoractual(target.value)
      this.datos2 = response.data.hor
      console.log(this.datos2[0])
    },
    logout () {
      localStorage.removeItem('jwtToken')
      this.$router.push({
        name: 'login'
      })
    }
  }
}
</script>

