<table id="customers">
    <thead>
      <tr>
        <th>Nombre usuario</th>
        <th>Cedula Id</th>
        <th>Funciones</th>
      </tr>
    </thead>
    <tbody>
      <tr class="success" v-for="dato in datos">
        <td>{{ dato.nombre }}</td>
        <td>{{ dato.doc_identidad }}</td>
        <td><template class="form-control" v-if="dato.estado === 1">
          <button class="btn btn-primary" @click="desactivar(dato.doc_identidad)">Desact</button>
</template>
<template class="form-control" v-if="dato.estado === 0">
<button class="btn btn-success" @click="activar(dato.doc_identidad)">Activar</button>
</template>
<router-link v-bind:to="{ name: 'editusu', params: { id: dato.doc_identidad } }">Edit</router-link>
<button class="btn btn-danger" @click="deleteuser(dato.doc_identidad)">Delete</button>
</td>
      </tr>      
