</template>

<script>

import PostsService from '@/services/PostsService'
export default {
  name: 'person',
  data () {
    return {
      datos: [],
      nombre1: JSON.parse(localStorage.getItem('jwtToken')).data.user
    }
  },
  methods: {
    async getdatos ({type, target}) {
      console.log(target.value)
      const response = await PostsService.mostrarusu(target.value)
      this.datos = response.data.user
      console.log(this.datos)
    },
    async deleteuser (id) {
      console.log(id)
      const $this = this
      $this.$swal({
        title: 'Esta apunto de eliminar un usuario',
        text: "You won't",
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Borrar usuario'
      }).then(function () {
        console.log(id)
        PostsService.deleteuser(id)
        $this.$router.go({
          path: '/'
        })
      })
    },
    async activar (id) {
      const $this = this
      $this.$swal({
        title: 'Esta a punto de activar',
        text: "You won't",
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'activar usuario'
      }).then(function () {
        PostsService.putact(id)
        $this.$router.go({
          path: '/'
        })
      })
    },
    async desactivar (id) {
      const $this = this
      $this.$swal({
        title: 'Esta apunto de desactivar',
        text: "You won't",
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'desactivar usuario usuario'
      }).then(function () {
        PostsService.putdes(id)
        $this.$router.go({
          path: '/'
        })
      })
    },
    logout () {
      localStorage.removeItem('jwtToken')
      this.$router.push({
        name: 'login'
      })
    }
  }
}
</script>

