import Api from '@/services/Api'

export default {
  fetchPosts () {
    return Api().get('a/posts')
  },

  addPost (params) {
    return Api().post('a/add_post', params)
  },

  updatePost (params) {
    return Api().put('a/posts/' + params.id, params)
  },

  getPost (params) {
    return Api().get('a/post/' + params.id)
  },
  getmat (params) {
    return Api().get('admin/getmat/' + params.id)
  },
  deleteuser (id) {
    return Api().delete('admin/deleteuser/' + id)
  },
  deletemat (id) {
    return Api().delete('admin/deletemat/' + id)
  },
  putact (id) {
    return Api().put('admin/putestactivar/' + id)
  },
  putdes (id) {
    return Api().put('admin/putestdesactivar/' + id)
  },
  mostrarpais () {
    return Api().get('admin/pais')
  },
  mostrardatos () {
    return Api().get('admin/datos')
  },
  mostrardepto (id) {
    return Api().get('admin/depto/' + id)
  },
  mostrarprov (id) {
    return Api().get('admin/prov/' + id)
  },
  mostrarhoractual (id) {
    return Api().get('admin/horario/' + id)
  },
  mostrarusu (id) {
    return Api().get('admin/usuarios/' + id)
  },
  datosusuario (id) {
    return Api().get('admin/datosusuario/' + id)
  },
  mostrarotros (id) {
    return Api().get('admin/materias')
  },
  adduser (params) {
    return Api().post('admin/add_user', params)
  },
  addmat (params) {
    return Api().post('admin/add_mat', params)
  },
  actualizarmat (params) {
    return Api().put('admin/actualizar_mat', params)
  },
  addhor (params) {
    return Api().post('admin/add_hor', params)
  },
  logger (params) {
    return Api().post('login', params)
  },
  obtenmaterias () {
    return Api().get('admin/materi')
  }
}



